	module.exports = {"data":{"site":{"siteMetadata":{"title":"Blog","author":"lxynox"}},"markdownRemark":{"id":"/private/tmp/blog/src/posts/bottom-up/index.md absPath of file >>> MarkdownRemark","html":"<h1>Background</h1>\n<p>I’ve been looking for a new job for quite while, it’s not as smooth as i was first expected. In fact, it’s way worse than i could imagine,\nand might be another one of the best lessons i’ve learned so far.</p>\n<h1>Reason</h1>\n<p>The difficulty comes from lacking of understanding of:</p>\n<p>a) the hiring process starting from the scanning of resume<br>\nb) measurements used to justify qualified candidates<br>\nc) the importance of cultural fit during typical software engineer interviews<br>\nEach parts play their own role across the whole process, you’re gonna pay tuition if underestimated any of them.</p>\n<h1>Emotion</h1>\n<p>It’s kinda hard to accurately capture all the feelings. Angry, frustration and self-skeptical.</p>\n<h1>Action</h1>\n<p>Now, i’m right at the <em>bottom</em> of page, already nothing to lose.</p>\n<h1>Conclusion</h1>\n<p>Good reading materials i came across those days:</p>\n<ol>\n<li><a href=\"https://news.ycombinator.com/item?id=11579757\">Hiring Is Broken – My interview experience in the tech industry</a></li>\n<li><a href=\"https://www.quora.com/What-is-the-best-career-advice-for-a-software-developer-to-earn-a-higher-salary\">What is the best career advice for a software developer to earn a higher salary?</a></li>\n</ol>","frontmatter":{"title":"Bottom Up","date":"April 05, 2017"}}},"pathContext":{"slug":"/bottom-up/","previous":{"fields":{"slug":"/diy-mobile-desktop/"},"frontmatter":{"title":"DIY mobile desktop"}},"next":null}}

